import { palette } from "../../../../palette";
import Input from "../../../../components/inputs/input/input";
import { Formik, Form} from "formik";
import YesButton from "../../../../components/buttons/yesButton/yesButton";
import Button from "../../../../components/buttons/button/button";
import NoButton from "../../../../components/buttons/noButton/noButton";
import { useSnackbar } from 'notistack';
import { confirmContext } from "../../../../contexts/confirmContext";
import React from "react";

export default function Admin({admin, privacyFilter}){

    const handleDeleteAccessUrl = () => {

    };
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const confirm = React.useContext(confirmContext);
    

    const handleSubmit = (newName) => {

    }

    const handleDelete = () => {
      confirm({
        handleSuccess: () => {
            // TODO API
            enqueueSnackbar("administrator deleted", { variant: "warning" });
        },
        handleCancel: () => {
            enqueueSnackbar("administrator not deleted", { variant: "success" });
        }});
    }

    return <div className='py-4'><Formik
    initialValues={admin}
    onSubmit={(values, { setSubmitting, setErrors}) => {
      console.log(values);
       handleSubmit(values.name).then(() => {
         setSubmitting(false);
       }, () => {
         setErrors({comment: "unable to finalize request"});
         setSubmitting(false);
       });
    }}
  >
    {({ isSubmitting }) => (
      <Form>
        
        <div className='flex pt-2'>
            <div className={`flex-grow ${privacyFilter? 'filter blur-sm': ''}`}>
            <Input disabled name="email"/>
            </div>

            <NoButton text="delete" onClick={handleDelete} disabled={isSubmitting} />
        </div>

        </Form>
                
    )}
  </Formik></div>
}