import React, { useEffect } from 'react';
import Zone from './components/zone/zone';
import YesButton from '../../components/buttons/yesButton/yesButton';
import Restaurant from './components/restaurant/restaurant';
import Admin from './components/admin/admin';
import { getRestaurants } from './services/restaurants';
import { useSnackbar } from 'notistack';
import { useState } from 'react';
function AdminDashboardPageSuper({privacyFilter, setRestaurant}) {
  const [restaurants, setRestaurants] = useState([]);
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  useEffect(() => {
    getRestaurants()
      .then(restaurants => setRestaurants(restaurants))
      .catch(_ => enqueueSnackbar("unable to retrieve restaurants", {variant:'error'}))
  });
  return <React.Fragment>
    <Zone title="restaurants">
    <div className='grid grid-cols-1 divide-y divide-gray-500'>
        <Restaurant restaurant={
          { name: 'restaurant a21', id: 'a21' }
        } setRestaurant={setRestaurant}/>
        <Restaurant restaurant={
          { name: 'restaurant a22', id: 'a22' }
        } setRestaurant={setRestaurant}/>
      </div>
    <div className='grid justify-items-center pt-8'>
    <YesButton onClick={() => {}} text={"ADD NEW"} w='w-64'/>
      </div>

  </Zone>

  

  <Zone title="root administrators">
  <div className='grid grid-cols-1 divide-y divide-gray-500'>
  <Admin admin={
          { email: 'admin@gmail.com', id: 'ad22' }
        } privacyFilter={privacyFilter}/>

<Admin admin={
          { email: 'admin2@gmail.com', id: 'ad23' }
        } privacyFilter={privacyFilter}/>
</div>
  <div className='grid justify-items-center  pt-8'>
    <YesButton onClick={() => {}} text={"ADD NEW"} w='w-64'/>
      </div>

  </Zone>
  </React.Fragment>
}

export default AdminDashboardPageSuper;

